/*-----------------------------------------
* dialog
*-----------------------------------------*/
/**
 * loading Image
 */
@keyframes modalLoaderSprite {
  from {
    background-position: 0; }
  to {
    background-position: -690px; } }

.modal-loader {
  width: 46px;
  height: 48px;
  margin: 5% auto 10% auto;
  background-repeat: no-repeat;
  background-image: url(data:image/png;base64,);
  background-size: 690px;
  animation: modalLoaderSprite 2s steps(15, end) infinite; }

@keyframes loaderSprite {
  from {
    background-position: 0; }
  to {
    background-position: -384px; } }

.spinner {
  width: 32px;
  height: 32px;
  background-repeat: no-repeat;
  background-image: url(data:image/png;base64,);
  background-size: 384px;
  animation: loaderSprite 1s steps(12, end) infinite;
  display: inline-block; }

.loader {
  width: 74px;
  height: 74px;
  margin: 0 auto 0 auto;
  text-align: center;
  vertical-align: middle;
  display: block;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 8px;
  padding: 20px;
  margin-top: -37px;
  margin-left: -37px;
  top: 50%;
  left: 50%;
  position: absolute; }

/**
 * overlay
 */
#modal-overlay {
  width: 100%;
  height: 120%;
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.7);
  display: none;
  z-index: 1000; }

.modal-box {
  position: fixed;
  background: #fff;
  border: solid 2px #000;
  box-sizing: border-box;
  width: 90%;
  display: none;
  z-index: 1001; }
  .modal-box .modal-loadMsg {
    text-align: center;
    font-size: 1.1rem; }
    .modal-box .modal-loadMsg dl dt {
      font-weight: bold;
      background: #15943a;
      color: #fff;
      margin-bottom: 10px; }
    .modal-box .modal-loadMsg dl dd {
      padding: 0 5% 0 5%;
      margin-bottom: 10px; }
  .modal-box .btnList {
    margin-bottom: 10px; }
    .modal-box .btnList li {
      display: inline-block;
      width: 45%;
      margin-left: 3%; }
      .modal-box .btnList li:first-child {
        margin-left: 0; }

